.guessTheNote {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.question {
  margin-top: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.answers {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 50px;
  width: 100%;
  height: 100px;
}

.sound {
  width: 100px;
  height: 70px !important;
  font-size: 1.5em !important;
}

.difficulties {
  width: 50%;
  margin-top: 25px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between; 
  width: 800px;
  height: 400px;
  margin: 100px auto !important;
  /* border: 1px solid rgb(167, 167, 167) !important;
  border-radius: 5px !important; */
  overflow: hidden;
}

.card-image-title {
  display: flex;
  align-items: center;
  width: 100%;
  height: 50%;
  background: url(/static/media/piano2.01fbfe17.png);
  background-size: cover;
}

.card-title {
  color: rgb(244, 244, 244);
  font-size: 5em !important ;
  font-weight: bold !important;
  margin-top: 90px;
}

.marginLeft {
  margin-left: 20px !important;
}

.container {
  width: 80%;
  margin: auto;
}

.card-action {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.typist {
  margin-top: 50px;
  text-align: center;
  font-size: 2em;
}

/* general css */

form {
  margin-top: 50px;
}
body {
  color: rgb(87, 87, 87);
  background-color: rgb(244, 244, 244);
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  color: white;
}
.brand-logo {
  left: 50px !important;
}

.logo {
  display: flex;
  align-items: center;
  height: 100%;
  margin: 0;
  text-decoration: none;
  color: white;
  font-size: 2em;
}

.header h1 a {
  display: flex;
  align-items: center;
  height: 100%;
  color: white;
  text-decoration: none;
}

.navigation {
  display: flex;
  height: 100%;
}

.navigation a {
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 20px;
  color: white;
  text-decoration: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.navigation a:hover {
  background-color: rgb(60, 76, 131);
  cursor: pointer;
}

.logo {
  padding: 0 20px;
}

